"use client";

import { useEffect, useState, useRef, use } from "react";
import { useRouter } from "next/navigation";
import { useUser } from "@clerk/nextjs";

export default function VoyageEditPage({ params }: { params: { id: string } | Promise<{ id: string }> }) {
  // ü§è Utilisation de use() pour r√©soudre le param si c'est une Promise
  const { id: voyageId } = use(params);

  const { user, isLoaded } = useUser();
  const router = useRouter();

  const [voyage, setVoyage] = useState<any | null>(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const progInput = useRef<HTMLInputElement | null>(null);
  const filesInput = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    if (!isLoaded || !user) return;

    const fetchVoyage = async () => {
      try {
        const res = await fetch(`/api/travels/get?voyageId=${voyageId}`);
        if (!res.ok) throw new Error("Voyage introuvable");
        const data = await res.json();
        setVoyage(data);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchVoyage();
  }, [isLoaded, user, voyageId]);
  if (!isLoaded) return <div>Chargement‚Ä¶</div>;
  if (!user) return <div>Veuillez vous connecter.</div>;
  if (loading) return <div>Chargement du voyage‚Ä¶</div>;
  if (error) return <div className="text-red-600">{error}</div>;
  if (!voyage) return <div>Voyage introuvable.</div>;

  async function uploadFile(file: File) {
    const res = await fetch("/api/s3/presign-upload", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ voyageId, filename: file.name, type: file.type }),
    });
    const { uploadUrl, fileUrl } = await res.json();
    await fetch(uploadUrl, { method: "PUT", body: file, headers: { "Content-Type": file.type } });
    return fileUrl;
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setSaving(true);

    try {
      const form = e.target as HTMLFormElement;
      const formData = new FormData(form);

      // Upload programme si chang√©
      let programme = voyage.programme;
      if (progInput.current?.files?.[0]) {
        const file = progInput.current.files[0];
        const url = await uploadFile(file);
        programme = { filename: file.name, url };
      }

      // Upload nouvelles pi√®ces jointes
      const existingPJ = voyage.pieces_jointes || [];
      const pjFiles = filesInput.current?.files || [];
      const uploadedPJ = [];
      for (const file of pjFiles) {
        const url = await uploadFile(file);
        uploadedPJ.push({ filename: file.name, url });
      }

      const body = {
        ...voyage,
        lieu: formData.get("lieu"),
        activite: formData.get("activite"),
        date_depart: formData.get("date_depart"),
        date_retour: formData.get("date_retour"),
        classes: formData.get("classes"),
        effectif_eleves: Number(formData.get("effectif_eleves")),
        effectif_accompagnateurs: Number(formData.get("effectif_accompagnateurs")),
        commentaire: formData.get("commentaire") || "",
        programme,
        pieces_jointes: [...existingPJ, ...uploadedPJ],
      };

      const res = await fetch(`/api/travels/update?voyageId=${voyageId}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body),
      });

      if (!res.ok) {
        const json = await res.json();
        throw new Error(json.error || "Erreur lors de la mise √† jour");
      }

      router.push("/travels");
    } catch (err: any) {
      setError(err.message);
    } finally {
      setSaving(false);
    }
  }

  return (
    <form onSubmit={handleSubmit} className="pt-[15vh] flex flex-col gap-4 max-w-xl mx-auto">
      <h2 className="text-xl font-bold mb-4">Modifier le voyage</h2>

      <label>
        Lieu / destination :
        <input name="lieu" defaultValue={voyage.lieu} required />
      </label>

      <label>
        Activit√© / motif :
        <input name="activite" defaultValue={voyage.activite} required />
      </label>

      <label>
        Date de d√©part :
        <input type="date" name="date_depart" defaultValue={voyage.date_depart} required />
      </label>

      <label>
        Date de retour :
        <input type="date" name="date_retour" defaultValue={voyage.date_retour} required />
      </label>

      <label>
        Classes concern√©es :
        <input name="classes" defaultValue={voyage.classes} required />
      </label>

      <label>
        Nombre d‚Äô√©l√®ves :
        <input type="number" name="effectif_eleves" min={1} defaultValue={voyage.effectif_eleves} required />
      </label>

      <label>
        Nombre d‚Äôaccompagnateurs :
        <input type="number" name="effectif_accompagnateurs" min={1} defaultValue={voyage.effectif_accompagnateurs} required />
      </label>

      <label>
        Programme (PDF ou image) :
        <input ref={progInput} type="file" accept=".pdf,.doc,.docx,image/*" />
      </label>

      <label>
        Autres pi√®ces jointes :
        <input ref={filesInput} type="file" multiple accept="image/*,.pdf" />
      </label>

      <label>
        Commentaire :
        <textarea name="commentaire" defaultValue={voyage.commentaire || ""} />
      </label>

      <button type="submit" disabled={saving} className="px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700">
        {saving ? "Enregistrement‚Ä¶" : "Mettre √† jour"}
      </button>

      {error && <p className="text-red-600 mt-2">{error}</p>}
    </form>
  );
}
